<template>
  <v-footer
    color="primary lighten-1"
    padless
  >
    <v-row
      justify="center"
      no-gutters
    >
      <v-btn
        v-for="link in links"
        :key="link"
        color="white"
        text
        rounded
        class="my-2"
        @click="abrirModulo(link)"
      >
          {{ link }}
      </v-btn>
      <v-col
        class="primary lighten-2 py-4 text-center white--text"
        cols="12"
      >
          <strong>Copyright</strong> — {{ new Date().getFullYear() }}
      </v-col>
    </v-row>
  </v-footer>
</template>



<script>
  export default {
    data: () => ({
      links: [
        'Inicio',
        'Eventos',
        'Sobre Nosotros',
        'Contactanos',
        'Crea Evento',
        'Inscribirse Evento',
      ],
    }),
    methods: {
      abrirModulo(link)
      {
        if(link=='Inicio') this.$router.push('./')
        if(link=='Eventos') this.$router.push('./eventos')
        if(link=='Sobre Nosotros') this.$router.push('./sobreNosotros')
        if(link=='Contactanos') this.$router.push('./contactanos')
        if(link=='Crea Evento') this.$router.push('./creaEvento')
        if(link=='Inscribirse Evento') this.$router.push('./inscribirseEvento')
      }
    }
  }
</script>